import logo from "./logo.svg";
import "./App.css";
import { PeraWalletConnect } from "@perawallet/connect";
import { useEffect, useState } from "react";
import freeportTeal from "./contracts/freeport.teal";
import replicantTeal from "./contracts/freeport.teal";
import clearTeal from "./contracts/stateless.teal";
import { compileProgram, getSetPricetxn } from "./utils/utils.js";
import algosdk from "algosdk";

const peraWallet = new PeraWalletConnect();
const token = "";
const server = "https://testnet-api.algonode.cloud";
const port = 443;
const client = new algosdk.Algodv2(token, server, port);

function App() {
  const [accountAddress, setAccountAddress] = useState(null);
  const [isConnectedToPeraWallet, setPeraWalletConection] = useState(false);
  const [btnText, setBtnText] = useState("Connect Wallet");
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    // Reconnect to the session when the component is mounted
    peraWallet.reconnectSession().then((accounts) => {
      // Setup the disconnect event listener
      peraWallet.connector?.on("disconnect", handleDisconnectWalletClick);

      if (accounts.length) {
        setAccountAddress(accounts[0]);
        setPeraWalletConection(true);
      }
    });
  }, []);

  function handleDisconnectWalletClick() {
    peraWallet.disconnect();
    setPeraWalletConection(false);
    setAccountAddress(null);
  }

  function handleConnectWalletClick() {
    peraWallet
      .connect()
      .then((newAccounts) => {
        // Setup the disconnect event listener
        peraWallet.connector?.on("disconnect", handleDisconnectWalletClick);
        setAccountAddress(newAccounts[0]);
        setPeraWalletConection(true);
      })
      .catch((err) => {
        console.error(err);
      });
  }
  const updateFreeportApp = async () => {
    if (!accountAddress) {
      alert("make sure to conect to a wallet");
      return;
    }
    setLoading(true);
    let appIndex = 245558018;
    let params = await client.getTransactionParams().do();
    let approvalProgramStr = await (await fetch(freeportTeal)).text();
    let clearProgramStr = await (await fetch(clearTeal)).text();
    let approvalProgram = await compileProgram(client, approvalProgramStr);
    let clearProgram = await compileProgram(client, clearProgramStr);
    const appUpdateTxn = await algosdk.makeApplicationUpdateTxnFromObject({
      from: accountAddress,
      lease: undefined,
      note: undefined,
      suggestedParams: params,
      appIndex,
      appArgs: undefined,
      rekeyTo: undefined,
      approvalProgram,
      clearProgram,
      accounts: undefined,
      boxes: undefined,
      foreignApps: undefined,
      foreignAssets: undefined,
    });
    console.log({ appUpdateTxn });
    const signerTransaction = [
      { txn: appUpdateTxn, signers: [accountAddress] },
    ];
    try {
      const signedTxn = await peraWallet.signTransaction([signerTransaction]);
      const sentTxn = await client.sendRawTransaction(signedTxn).do();
      console.log(signedTxn);
      alert("Updated successfully");
      setLoading(false);
    } catch (error) {
      setLoading(false);
      console.log("Couldn't sign Opt-in txns", error);
    }
  };
  //TODO: put the right ids
  const updateEchoesPrice = async () => {
    if (!accountAddress) {
      alert("make sure to conect to a wallet");
      return;
    }
    setLoading(true);
    let appIds = [
      604060103, 604060129, 604060130, 604060131, 604060132, 604060133,
      604060134, 604060135, 604060136,
    ];
    let appIndex = 0;
    let allTxns = [];
    for (let i = 0; i < appIds.length; i++) {
      let params = await client.getTransactionParams().do();
      const updatePriceTxn = getSetPricetxn(appIndex, accountAddress, params);
      allTxns.push(updatePriceTxn);
    }
    let groupId = algosdk.computeGroupID(allTxns);
    allTxns = allTxns.map((el) => {
      el.group = groupId;
      return el;
    });

    let signerTransaction = allTxns.map((txn) => ({
      txn,
      signers: [accountAddress],
    }));
    try {
      const signedTxn = await peraWallet.signTransaction([signerTransaction]);
      const sentTxn = await client.sendRawTransaction(signedTxn).do();
      console.log(signedTxn);
      setLoading(false);
      alert("tranaction sucessful");
    } catch (error) {
      alert(error);
      console.log("Couldn't sign Opt-in txns", error);
      setLoading(false);
    }
  };

  const updateReplicantApp = async () => {
    if (!accountAddress) {
      alert("make sure to conect to a wallet");
      return;
    }
    setLoading(true);
    let appIds = [
      387031352, 387031353, 387031354, 387031355, 387031356, 387031357,
      387031358, 387031359, 387031360, 387031361, 387031362, 387031363,
      387031364, 387031365, 387031366, 387034557, 387034558, 387034559,
      387034560, 387034561,
    ];
    let allTxns = [];
    //first Half
    for (let i = 0; i < appIds.length / 2; i++) {
      let appIndex = appIds[i];
      let params = await client.getTransactionParams().do();
      let approvalProgramStr = await (await fetch(replicantTeal)).text();
      let clearProgramStr = await (await fetch(clearTeal)).text();
      let approvalProgram = await compileProgram(client, approvalProgramStr);
      let clearProgram = await compileProgram(client, clearProgramStr);
      const appUpdateTxn = await algosdk.makeApplicationUpdateTxnFromObject({
        from: accountAddress,
        lease: undefined,
        note: undefined,
        suggestedParams: params,
        appIndex,
        appArgs: undefined,
        rekeyTo: undefined,
        approvalProgram,
        clearProgram,
        accounts: undefined,
        boxes: undefined,
        foreignApps: undefined,
        foreignAssets: undefined,
      });
      allTxns.push(appUpdateTxn);
    }
    let firstGroupId = algosdk.computeGroupID(allTxns);
    allTxns = allTxns.map((el) => {
      el.group = firstGroupId;
      return el;
    });

    let allFirstSignerTransactions = allTxns.map((txn) => ({
      txn,
      signers: [accountAddress],
    }));

    try {
      const firstSignedTxn = await peraWallet.signTransaction([
        allFirstSignerTransactions,
      ]);
      const firstSentTx = await client.sendRawTransaction(firstSignedTxn).do();
      console.log(firstSignedTxn);
    } catch (error) {
      console.log("Couldn't sign Opt-in txns", error);
    }
    allTxns = [];
    for (let i = appIds.length / 2; i < appIds.length; i++) {
      let appIndex = appIds[i];
      let params = await client.getTransactionParams().do();
      let approvalProgramStr = await (await fetch(replicantTeal)).text();
      let clearProgramStr = await (await fetch(clearTeal)).text();
      let approvalProgram = await compileProgram(client, approvalProgramStr);
      let clearProgram = await compileProgram(client, clearProgramStr);
      const appUpdateTxn = await algosdk.makeApplicationUpdateTxnFromObject({
        from: accountAddress,
        lease: undefined,
        note: undefined,
        suggestedParams: params,
        appIndex,
        appArgs: undefined,
        rekeyTo: undefined,
        approvalProgram,
        clearProgram,
        accounts: undefined,
        boxes: undefined,
        foreignApps: undefined,
        foreignAssets: undefined,
      });
      allTxns.push(appUpdateTxn);
    }
    let secondGroupId = algosdk.computeGroupID(allTxns);
    allTxns = allTxns.map((el) => {
      el.group = secondGroupId;
      return el;
    });

    let allSecondSignerTransactions = allTxns.map((txn) => ({
      txn,
      signers: [accountAddress],
    }));
    try {
      const firstSignedTxn = await peraWallet.signTransaction([
        allSecondSignerTransactions,
      ]);
      const secondSrntTx = await client.sendRawTransaction(firstSignedTxn).do();
      console.log(firstSignedTxn);
      setLoading(false);
      alert("transaction successful");
    } catch (error) {
      setLoading(false);
      console.log("Couldn't sign Opt-in txns", error);
    }
  };

  return (
    <div className="flex flex-col items-center">
      <div className="w-10/12">
        {/* //Header */}
        <div className="flex flex-row justify-end">
          <button
            className="bg-blue-500 rounded-md border-0 p-2 m-8 w-56
           text-white font-bold hover:scale-x-110 hover:scale-y-110
            duration-500 shadow-2xl truncate"
            onClick={
              isConnectedToPeraWallet
                ? handleDisconnectWalletClick
                : handleConnectWalletClick
            }
          >
            {accountAddress !== null ? accountAddress : "Connect wallet"}
          </button>
        </div>

        {loading && (
          <div className="flex justify-center">
            <div>
              <span className="loading loading-bars loading-lg text-info"></span>
            </div>
          </div>
        )}
        {/* Body */}
        <div className="flex flex-col items-center mt-40">
          <div className="w-10/12 flex flex-row justify-around">
            <button
              className="bg-blue-500 rounded-md border-0 p-2 m-8 w-56
           text-white font-bold hover:scale-x-110 hover:scale-y-110
            duration-500 shadow-2xl"
              onClick={updateFreeportApp}
            >
              Update Freeport
            </button>

            <button
              className="bg-blue-500 rounded-md border-0 p-2 m-8 w-56
                  text-white font-bold hover:scale-x-110 hover:scale-y-110
            duration-500 shadow-2xl"
              onClick={updateReplicantApp}
            >
              Update Replicants
            </button>

            <button
              className="bg-blue-500 rounded-md border-0 p-2 m-8 w-56
                  text-white font-bold hover:scale-x-110 hover:scale-y-110
                    duration-500 shadow-2xl"
              onClick={updateEchoesPrice}
            >
              Update Echoes
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
