import algosdk from "algosdk";

export async function compileProgram(
  client,
  programSource
) {
  return new Promise(async (resolve, reject) => {
    try {
      const results = await client.compile(programSource).do();
      const compiledBytes = new Uint8Array(
        Buffer.from(results.result, "base64")
      );
      resolve(compiledBytes);
    } catch (error) {
      reject(error);
    }
  });
}


export  function getSetPricetxn(appId, from, params){
  const textEncoder = new TextEncoder();
  const appCallTxn = algosdk.makeApplicationNoOpTxn(
    from,
    params,
    appId,
    [textEncoder.encode(Buffer.from("set_price").toString()),
    algosdk.encodeUint64(44444000000)],
    undefined,
    undefined,
    undefined,
    undefined,
    undefined,
    undefined,
  );
  return appCallTxn;
}
